<template>
    <section class="page-none">
        <div class="page-none-wrapper">
            404
        </div>
    </section>
</template>

<script>

    export default {
        name: 'page-none',
        data () {
            return {
                lightNum: 2
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" rel="stylesheet/less" scoped>
    .page-none {
        position: absolute;
        width: 100%;
        height: 100%;
        .page-none-wrapper {
            position: absolute;
            font-size: 60px;
            color: #FFF;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            background-color: black;
        }
    }
</style>



